<template>
    <div class="row">
        <div class="col-md-12">
            <div class="card shadow">
                <div class="card-header">
                    <h5 class="text-secondary">Partes Procesales</h5>
                </div>
                <div class="card-body">
                    <div class="row mb-3">
                        <div class="col-md-4">
                            <div class="form-group">
                                <label class="form-label">Tipo de parte procesal</label>
                                <select class="form-control" v-model="tipo_procesal" @change="validarTipoParte"
                                    :class="{ 'has-error': errores.tipo_procesal }">
                                    <option value="" disabled selected class="py-5">Seleccione una opción</option>
                                    <option value="DEMANDANTE" class="py-3">Demandante</option>
                                    <option value="DEMANDADO">Demandado</option>
                                </select>
                                <span class="error-message" v-if="errores.tipo_procesal">Complete este campo para
                                    continuar.</span>
                            </div>
                        </div>
                        <div class="col-md-4">
                            <div class="form-group">
                                <label class="form-label">Tipo de persona</label>
                                <select class="form-control" v-model="tipo_persona" @change="validarTipoPersona"
                                    :class="{ 'has-error': errores.tipo_persona }">
                                    <!-- <option value="" disabled selected class="py-5">Seleccione una opción</option> -->
                                    <option value="NATURAL" class="py-3" selected>Persona Natural
                                    </option>
                                    <option value="JURIDICA">Persona Juridica</option>

                                </select>
                                <span class="error-message" v-if="errores.tipo_persona">Complete este campo para
                                    continuar.</span>
                            </div>
                        </div>
                        <div class="col-md-4">
                            <div class="form-group">
                                <label class="form-label">Condición de demandante</label>
                                <select class="form-control" v-model="condicion1"
                                   >
                                    <option value="" disabled selected class="py-5">Seleccione una opción</option>
                                    <option value="DOCENTE UNIVERSITARIO" class="py-3" selected>Docente Universitario
                                    </option>
                                    <option value="ESTUDIANTE">Estudiante</option>
                                    <option value="ADMINISTRATIVO">Administrativo</option>
                                    <option value="TERCERO">Tercero</option>

                                </select>
                                <span class="error-message" v-if="errores.tipo_persona">Complete este campo para
                                    continuar.</span>
                            </div>
                        </div>
                        <!-- <div class="col-md-6">
                            <button type="button" class="btn btn-primary btn-lg rounded ml-auto "
                                @click="mostrarPersona('NATURAL')">Persona Natural</button>
                            <button type="button" class="btn btn-primary btn-lg rounded ml-2"
                                @click="mostrarPersona('JURIDICA')">Persona Jurídica</button>
                        </div> -->
                    </div>
                    <div class="row mb-3">
                        <div class="col-md-12">
                            <div class="alert alert-primary py-3" role="alert">
                                Informacion Personal
                            </div>
                        </div>
                    </div>
                    <div v-if="tipo_persona === 'NATURAL'">
                        <div class="row">
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label" for="dni">DNI</label>
                                    <input type="text" class="form-control" id="dni" placeholder="12345678" v-model="pnat.nat_dni"
                                        @input="errores.natural_dni = false" @blur="validarDni"
                                        :class="{ 'has-error': errores.natural_dni }">
                                    <span class="error-message" v-if="errores.natural_dni">Complete este campo para
                                        continuar.</span>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label">Apellido
                                        Paterno</label>
                                    <input type="text" class="form-control" placeholder="Alarcon"
                                        v-model="pnat.nat_apellido_paterno"
                                        @input="errores.natural_apellido_paterno = false" @blur="validarApellidoPaterno"
                                        :class="{ 'has-error': errores.natural_apellido_paterno }">
                                    <span class="error-message" v-if="errores.natural_apellido_paterno">Complete este campo
                                        para
                                        continuar.</span>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label">Apellido
                                        Materno</label>
                                    <input type="text" class="form-control" placeholder="Ramirez"
                                        v-model="pnat.nat_apellido_materno"
                                        @input="errores.natural_apellido_materno = false" @blur="validarApellidoMaterno"
                                        :class="{ 'has-error': errores.natural_apellido_materno }">
                                    <span class="error-message" v-if="errores.natural_apellido_materno">
                                        Complete este campo para continuar.
                                    </span>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label">Nombres</label>
                                    <input type="text" class="form-control" placeholder="Jhon Doe" v-model="pnat.nat_nombres"
                                        @input="errores.natural_nombres = false" @blur="validarNombres"
                                        :class="{ 'has-error': errores.natural_nombres }">
                                    <span class="error-message" v-if="errores.natural_nombres">
                                        Complete este campo para continuar.
                                    </span>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label">Telefono</label>
                                    <input type="text" class="form-control" placeholder="980560345" v-model="pnat.nat_telefono">

                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                    <label class="form-label">Email
                                    </label>
                                    <input type="text" class="form-control" placeholder="example@gmail.com" v-model="pnat.nat_correo">

                                </div>
                            </div>
                        </div>
                    </div>
                    <div v-if="tipo_persona === 'JURIDICA'">
                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label" for="ruc">RUC</label>
                                    <input type="text" class="form-control" id="ruc" placeholder="" v-model="pj.jur_ruc"
                                        @input="errores.juridica_ruc = false" @blur="validarRuc"
                                        :class="{ 'has-error': errores.juridica_ruc }">
                                    <span class="error-message" v-if="errores.juridica_ruc">
                                        Complete este campo para continuar.
                                    </span>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label" for="razonSocial">Nombre o Razon
                                        Social</label>
                                    <input type="text" class="form-control" id="razonSocial" placeholder=""
                                        v-model="pj.jur_razon_social" @input="errores.juridica_razon_social = false"
                                        @blur="validarRazonSocial" :class="{ 'has-error': errores.juridica_razon_social }">
                                    <span class="error-message" v-if="errores.juridica_razon_social">
                                        Complete este campo para continuar.
                                    </span>
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label" for="telefono">Telefono</label>
                                    <input type="text" class="form-control" id="telefono" placeholder=""
                                        v-model="pj.jur_telefono">
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="form-group">
                                    <label class="form-label" for="email">Email
                                    </label>
                                    <input type="text" class="form-control" id="email" placeholder=""
                                        v-model="pj.jur_correo">
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row my-2">
                        <div class="col-md-12">
                            <div class="alert alert-primary py-3" role="alert">
                                Direccion
                            </div>
                        </div>
                    </div>
                    <div class="row mt-3">
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label" for="departamento">Departamento</label>
                                <select class="form-control" id="departamento" v-model="direccion.dep_id"
                                    @change="cargarProvincias" :class="{ 'has-error': errores.departamento }">
                                    <option value="" selected disabled>Seleccione una opción</option>
                                    <option v-for="departamento in departamentos" :value="departamento.dep_id">{{
                                        departamento.dep_nombre }}
                                    </option>
                                </select>
                                <span class="error-message" v-if="errores.departamento">
                                    Complete este campo para continuar.
                                </span>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label" for="provincia">Provincia</label>
                                <select class="form-control" id="provincia" v-model="direccion.pro_id"
                                    @change="cargarDistritos" :class="{ 'has-error': errores.provincia }">
                                    <option value="" selected disabled>Seleccione una opción</option>
                                    <option v-for="provincia in provincias" :value="provincia.pro_id">{{
                                        provincia.pro_nombre }}</option>
                                </select>
                                <span class="error-message" v-if="errores.provincia">
                                    Complete este campo para continuar.
                                </span>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label" for="distrito">Distrito</label>
                                <select class="form-control" id="distrito" v-model="direccion.dis_id" @change="validarDistrito" :class="{ 'has-error': errores.distrito }">
                                    <option value="" selected disabled>Seleccione una opción</option>
                                    <option v-for="distrito in distritos" :value="distrito.dis_id">{{ distrito.dis_nombre }}
                                    </option>
                                </select>
                                <span class="error-message" v-if="errores.distrito">
                                    Complete este campo para continuar.
                                </span>
                            </div>
                        </div>
                        <div class="col-md-6">
                            <div class="form-group">
                                <label class="form-label" for="calleAvenida">Calle, Avenida, Mz,
                                    S/N</label>
                                <input type="text" class="form-control" id="calleAvenida" placeholder="Av. Peru 2345"
                                    v-model="direccion.dir_calle_av" @input="validarCalleAvenida" :class="{ 'has-error': errores.calleAvenida}">
                                <span class="error-message" v-if="errores.calleAvenida">
                                    Complete este campo para continuar.
                                </span>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="d-flex">
                            <div class="ml-auto">
                                <button class="btn btn-secondary btn-lg rounded" type="button" @click="anteriorParte"><i
                                        data-feather="arrow-left"></i> Anterior</button>
                                <button class="btn btn-primary btn-lg rounded ml-2" type="button" @click="siguienteParte">
                                    Siguiente<i data-feather="arrow-right"></i>
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- <div class="card-footer border-top-0">
                    <div class="row">
                        <div class="d-flex">
                            <div class="ml-auto">
                                <button class="btn btn-secondary btn-lg rounded" type="button" @click="anteriorParte"><i
                                        data-feather="arrow-left"></i> Anterior</button>
                                <button class="btn btn-primary btn-lg rounded ml-2" type="button" @click="siguienteParte">
                                    Siguiente<i data-feather="arrow-right"></i>
                                </button>
                            </div>
                        </div>
                    </div>
                </div> -->
            </div>
        </div>
    </div>
</template>
  
<script>
import { mapActions } from "vuex";
import feather from 'feather-icons';

export default {
    name: 'RegistroProcesal',
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    props: {
        pn: {},
        pj: {},
        dir: {},
        tipopersona: "",
        procesal: "",
        condicion:""
    },
    data() {
        return {
            esPersonaNatural: true,
            departamentos: [],
            provincias: [],
            distritos: [],
            direccion: {
                dir_calle_av: "",
                dis_id: "",
                pro_id: "",
                dep_id: ""
            },
            pnat: {
                nat_dni: "",
                nat_apellido_paterno: "",
                nat_apellido_materno: "",
                nat_nombres: "",
                nat_telefono: "",
                nat_correo: ""
            },
            pjuc: {
                jur_ruc: "",
                jur_razon_social: "",
                jur_telefono: "",
                jur_correo: "",
                jur_rep_legal: ""
            },
            tipo_persona: "",
            tipo_procesal: "",
            condicion1:"",
            errores: {
                tipo_procesal: false,
                tipo_persona: false,
                natural_dni: false,
                natural_apellido_paterno: false,
                natural_apellido_materno: false,
                natural_nombres: false,
                juridica_ruc: false,
                juridica_razon_social: false,
                departamento: false,
                provincia: false,
                distrito: false,
                calleAvenida: false

            }
        };
    },
    async created() {
        this.pnat = this.pn;
        this.pjuc = this.pj;
        this.direccion = this.dir;
        this.tipo_persona = this.tipopersona;
        this.condicion1=this.condicion;
        if (this.tipo_persona == '') {
            this.tipo_persona = "NATURAL";
        } {
            if (this.tipo_persona == 'NATURAL') {
                this.tipo_persona = "NATURAL";
            } else {
                this.tipo_persona = "JURIDICA";
            }
        }
        this.tipo_procesal = this.procesal;
        if (this.tipo_procesal == '') {
            this.tipo_procesal = "DEMANDANTE";
        } {
            if (this.tipo_procesal == 'DEMANDANTE') {
                this.tipo_procesal = "DEMANDANTE";
            } else {
                this.tipo_procesal = "DEMANDADO";
            }
        }
        await this.cargarDepartamentos();
        if (this.direccion.dep_id) {
            await this.cargarProvincias();
        }

        if (this.direccion.pro_id) {
            await this.cargarDistritos();
        }
    },
    methods: {
        ...mapActions(['get', 'post']),

        async cargarDepartamentos() {
            try {

                const response = await this.get({
                    url: this.$store.getters.get__url + "/department",
                    token: this.$store.getters.get__token,
                    params: {}
                });
                this.departamentos = response.data;
                this.provincias = [];
                this.distritos = [];
            } catch (error) {
                console.error("Error al cargar departamentos:", error);
            }
        },
        async cargarProvincias() {
            if (this.direccion.dep_id) {
                try {

                    const response = await this.post({
                        url: this.$store.getters.get__url + "/department/provincias",
                        token: this.$store.getters.get__token,
                        params: {
                            dep_id: this.direccion.dep_id,
                        }
                    });
                    this.provincias = response.data;
                    this.distritos = [];
                    this.validarDepartamento();
                } catch (error) {
                    console.error("Error al cargar provincias:", error);
                }
            }
        },
        async cargarDistritos() {
            if (this.direccion.pro_id) {
                try {
                    const response = await this.post({
                        url: this.$store.getters.get__url + "/department/distritos",
                        token: this.$store.getters.get__token,
                        params: {
                            pro_id: this.direccion.pro_id,
                        }
                    });
                    this.distritos = response.data;
                    this.validarProvincia();
                } catch (error) {
                    console.error("Error al cargar distritos:", error);
                }
            }
        },
        //validaciones
        validarTipoParte() {
            if (this.tipo_procesal === "") {
                this.errores.tipo_procesal = true;
            } else {
                this.errores.tipo_procesal = false;
            }
        },
        validarTipoPersona() {
            if (this.tipo_persona === "") {
                this.errores.tipo_persona = true;
            } else {
                this.errores.tipo_persona = false;
            }
        },
        validarDni() {
            if (this.pnat.nat_dni === "") {
                this.errores.natural_dni = true;
            } else {
                this.errores.natural_dni = false;
            }
        },
        validarApellidoPaterno() {
            if (this.pnat.nat_apellido_paterno === "") {
                this.errores.natural_apellido_paterno = true;
            } else {
                this.errores.natural_apellido_paterno = false;
            }
        },
        validarApellidoMaterno() {
            if (this.pnat.nat_apellido_materno === "") {
                this.errores.natural_apellido_materno = true;
            } else {
                this.errores.natural_apellido_materno = false;
            }
        },
        validarNombres() {
            if (this.pnat.nat_nombres === "") {
                this.errores.natural_nombres = true;
            } else {
                this.errores.natural_nombres = false;
            }
        },
        validarRuc() {
            if (this.pjuc.jur_ruc === "") {
                this.errores.juridica_ruc = true;
            } else {
                this.errores.juridica_ruc = false;
            }
        },
        validarRazonSocial() {
            if (this.pjuc.jur_razon_social === "") {
                this.errores.juridica_razon_social = true;
            } else {
                this.errores.juridica_razon_social = false;
            }
        },
        validarPersona() {
            if (this.tipo_persona === 'NATURAL') {
                this.validarDni();
                this.validarApellidoPaterno();
                this.validarApellidoMaterno();
                this.validarNombres();

                return (
                    !this.errores.natural_dni &&
                    !this.errores.natural_apellido_paterno &&
                    !this.errores.natural_apellido_materno &&
                    !this.errores.natural_nombres
                );
            } else {
                this.validarRuc();
                this.validarRazonSocial();
                return (
                    !this.errores.juridica_ruc &&
                    !this.errores.juridica_razon_social
                );
            }
        },
        validarDepartamento(){
            if(this.direccion.dep_id === ""){
                this.errores.departamento = true;
            }else{
                this.errores.departamento = false;
            }
        },
        validarProvincia(){
            if(this.direccion.pro_id === ""){
                this.errores.provincia = true;
            }else{
                this.errores.provincia = false;
            }
        },
        validarDistrito(){
            if(this.direccion.dis_id === ""){
                this.errores.distrito = true;
            }else{
                this.errores.distrito = false;
            }
        },
        validarCalleAvenida(){
            if(this.direccion.dir_calle_av === ""){
                this.errores.calleAvenida = true;
            }else{
                this.errores.calleAvenida = false;
            }
        },
        validarCampos() {
            this.validarTipoParte();
            this.validarTipoPersona();
            this.validarDepartamento();
            this.validarProvincia();
            this.validarDistrito();
            this.validarCalleAvenida();

            return (
                !this.errores.tipo_procesal &&
                !this.errores.tipo_persona &&
                !this.errores.departamento &&
                !this.errores.provincia &&
                !this.errores.distrito &&
                !this.errores.calleAvenida
            );
        },
      
        anteriorParte: function () {

            this.$emit('anteriorParte');
            this.$emit('guardarpn', this.pnat);
            this.$emit('guardarpj', this.pjuc);
            this.$emit('guardardir', this.direccion);
            this.$emit('guardartipopersona', this.tipo_persona);
            this.$emit('guardarprocesal', this.tipo_procesal);
            this.$emit('guardarcondicion', this.condicion1);

        },
        siguienteParte: function () {
            if (this.validarPersona() && this.validarCampos()) {
                this.$emit('siguienteParte');
                this.$emit('guardarpn', this.pnat);
                this.$emit('guardarpj', this.pjuc);
                this.$emit('guardardir', this.direccion);
                this.$emit('guardartipopersona', this.tipo_persona);
                this.$emit('guardarprocesal', this.tipo_procesal);
                this.$emit('guardarcondicion', this.condicion1);
                console.log(this.tipo_procesal);
            }
        },
    },
};
</script>
<style scoped>
.has-error {
    border: 1px solid #EA4D4D;
}

.valid-input{
    border: 1px solid #17C666;
}

.error-message {
    padding-left: 15px;
    color: #EA4D4D;
    font-size: 12px;
}
</style>
  